import React from 'react';
import { Platform, Linking, StyleSheet, View, TouchableOpacity, Image } from 'react-native';

export default function Contact() {
  function onPressContactHandler(url) {
    if(Platform.OS == "web") {
      window.open(url, '_blank');
    } else {
      Linking.openURL(url)
    }
  }

  return (
    <View style={styles.contactView}>
      <TouchableOpacity
      onPress={() => {onPressContactHandler("https://www.facebook.com/cortinasroller.laserena.3")}}>
        <Image
        source={require("../assets/facebook-icon.png")}
        style={styles.facebookIcon} />
      </TouchableOpacity>
      <TouchableOpacity
      onPress={() => {onPressContactHandler("https://wa.me/56983655707")}}>
        <Image
        source={require("../assets/whatsapp-icon.png")}
        style={styles.whatsappIcon} />
      </TouchableOpacity>
    </View>
  )
}

const styles = StyleSheet.create({
  contactView: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    position: 'absolute',
    padding: 8,
    right: 0,
    bottom: 0,
    backgroundColor: '#161e63',
    borderTopLeftRadius: 24,
  },
  facebookIcon: {
    width: 75,
    height: 75,
  },
  whatsappIcon: {
    marginTop: 5,
    width: 65,
    height: 65,
    tintColor: 'lightgreen',
    marginLeft: 8,
  },
});
