import React from 'react';
import { StyleSheet } from 'react-native';

import HomeScreen from './HomeScreen'
import GalleryScreen from './GalleryScreen'
import ContactScreen from './ContactScreen'
import ReviewsScreen from './ReviewsScreen'


export default function ScreenRender(props) {
  switch (props.screen) {
    case "Home": return <HomeScreen style={styles.screenContainerStyle} />;
    case "Gallery": return <GalleryScreen style={styles.screenContainerStyle} />;
    case "Contact": return <ContactScreen style={styles.screenContainerStyle} />;
    case "Reviews": return <ReviewsScreen style={styles.screenContainerStyle} />;
    default: return <HomeScreen />;
  }
}

const styles = StyleSheet.create({
  screenContainerStyle: {
    alignItems: 'center',
    textAlign: 'center',
    width: '80%',
    paddingTop: 24,
  },
});
