import React from 'react';
import { Linking, StyleSheet, View, TouchableOpacity, Text } from 'react-native';

const Link = props => {
  const focused = props.currentScreen === props.link;

  const touchableStyle = {
    padding: 12,
    borderTopLeftRadius: 16,
    borderTopRightRadius: 16,
    backgroundColor: focused ? '#26292F' : 'none',
  };

  const textStyle = {
    color: focused ? 'yellow' : 'white',
    fontWeight: 'bold',
    fontSize: 24,
  }

  return (
    <TouchableOpacity
    onPress={() => props.setScreen(props.link)}
    style={touchableStyle} >
      <Text style={textStyle}>{props.text}</Text>
    </TouchableOpacity>
  );
};

const Links = props => {
  return (
    <View style={styles.links}>
      <Link text="HOME" link="Home" currentScreen={props.currentScreen} setScreen={props.setScreen}/>
      <Link text="GALERÍA" link="Gallery" currentScreen={props.currentScreen} setScreen={props.setScreen}/>
      <Link text="CONTACTO" link="Contact" currentScreen={props.currentScreen} setScreen={props.setScreen}/>
      <Link text="OPINIONES" link="Reviews" currentScreen={props.currentScreen} setScreen={props.setScreen}/>
    </View>
  )
};

export default function Header(props) {
  return (
    <View style={styles.header}>
      <Text style={styles.title}>Cortinas Roller La Serena</Text>
      <Text style={styles.title}>MZRoller</Text>
      <View style={styles.separator} />
      <Links currentScreen={props.screen} setScreen={props.setScreen} />
    </View>
  );
};

const styles = StyleSheet.create({
  header: {
    backgroundColor: '#161e63',
    alignItems: 'center',
    width: '100%',
    paddingTop: 8,
  },
  title: {
    color: 'white',
    fontSize: 42,
    fontWeight: 'bold',
    textAlign: 'center',
  },
  separator: {
    borderColor: 'white',
    borderBottomWidth: 1,
    width: '90%',
  },
  links: {
    flexDirection: 'row',
    width: '100%',
    justifyContent: 'space-evenly',
    marginTop: 4,
  },
});
